.application {
  height: 100%;
  color: #e6e6e6;
  overflow: scroll;
  padding: 1rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  margin-left: -0.5rem;
  margin-right: -0.5rem;
}

.application .box {
  margin-bottom: 1rem;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  -ms-flex-preferred-size: 100%;
      flex-basis: 100%;
  -ms-flex-positive: 1;
      flex-grow: 1;
  width: 384px;
  min-height: 300px;
}

@media (min-width: 50rem) {
  .application .box {
    -ms-flex-preferred-size: 33%;
        flex-basis: 33%;
  }
}

@media (min-width: 80rem) {
  .application .box {
    -ms-flex-preferred-size: 25%;
        flex-basis: 25%;
  }
}

@media (min-width: 110rem) {
  .application .box {
    -ms-flex-preferred-size: 20%;
        flex-basis: 20%;
  }
}

.center {
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.pricedate {
  margin-top: 0;
  margin-bottom: -0.25rem;
  font-size: 12px;
  color: #767676;
}
.price {
  margin-top: 0;
  margin-bottom: 1rem;
}

.trend-up {
  width: 0;
  height: 0;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;

  border-bottom: 20px solid #00f700;
}
.trend-down {
  width: 0;
  height: 0;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;

  border-top: 20px solid #f00;
}



